import { useEffect, useState } from 'react'
import { Observable } from 'rxjs'

function initialObservableValue<T>(observable$: Observable<T>, defaultValue: T | null = null): T | null {
	let value = defaultValue

	observable$
		.subscribe(
			val => {
				value = val
			}
		)
		.unsubscribe()

	return value
}

export function useObservable<T>(observable: Observable<T> | (() => Observable<T>), initialValue: T | null = null): T | null {
	const [ stream ] = useState(observable)
	const [ value, setValue ] = useState(() => initialObservableValue(stream, initialValue))

	useEffect(() => {
		const subscription = stream.subscribe(next => {
			setValue(next)
		})
		return () => {
			subscription.unsubscribe()
		}
	}, [])

	return value
}
